@use "../../common/Variables/Variables.scss" as V;

ns-x-pride {

    display: block;
    width: 100%;
}

ns-x-pride .pride-x-component {
    height: 500px;
    // width: inherit;
    background-color: black;
    display: flex;
    flex-direction: column;
    font-family: V.$ns-common-font;
    display: flex;
    align-items: center;
    justify-content: center;

    .container {
        transition: all .5s;
        opacity: 0;
        transform: translateY(-100px);

        .welcome-text {
            width: 100%;
            height: 200px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            // flex-wrap: wrap;
            color: #fff;
            text-decoration: double;
            text-decoration-color: #fff;

            .xtr-2 {
                font-size: 13pt;
                color: rgba(255, 255, 255, 0.63);
                font-family: V.$ns-common-font;
                max-width: 100%;

            }

            .welcome-text-wrapper {
                font-size: 70pt;
                font-weight: bolder;
                font-family: V.$ns-common-font;


                &::after {
                    content: "... ";
                }

                &::before {
                    content: " ...";
                }
            }
        }


        .control-button-container {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 70px;

            ns-link {
                a {
                    text-decoration: none;
                }
                .expl-button-container {
                    font-family: V.$ns-common-font;
                    height: 50px;
                    width: 160px;
                    cursor: pointer;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border: 2px solid #fcfcfc;
                    color: #fff;
                    transition: all .5s;
                    background-color: #2179ec;
    
                    &:hover {
                        background-color: #2c2c2c;
                        cursor: pointer;
                    }
    
                    &:active {
                        transform: scale(0.8);
                    }
                }
            }

           

        }

    }


}